<template>
  <v-app-bar
    id="app-bar"
    absolute
    app
    color="transparent"
    flat
    height="75"
  >
    <v-btn
      class="mr-3"
      elevation="1"
      fab
      small
      @click="$vuetify.breakpoint.smAndDown ? setDrawer(!drawer) : $emit('input', !value)"
    >
      <v-icon v-if="value">
        mdi-view-quilt
      </v-icon>

      <v-icon v-else>
        mdi-dots-vertical
      </v-icon>
    </v-btn>

    <v-toolbar-title
      class="hidden-sm-and-down font-weight-light"
      v-text="$t($route.name)"
    />

    <v-spacer/>
    <v-menu
      bottom
      left
      offset-y
      origin="top right"
      transition="scale-transition"
    >
      <template v-slot:activator="{ attrs, on }">
        <v-btn
          class="ml-2"
          min-width="0"
          text
          v-bind="attrs"
          v-on="on"
        >
          <v-icon>mdi-web</v-icon>
        </v-btn>
      </template>

      <v-list
        :tile="false"
        nav
      >
        <v-list-item
          v-for="(n, i) in notifications"
          :key="i"
          @click="changeLang(i)"
        >
          <v-list-item-title
            v-text="n"
          />
        </v-list-item>
      </v-list>
    </v-menu>
    <v-btn
      class="ml-2"
      href="../index.html"
      text
    >
      <v-icon>
        mdi-home
      </v-icon>
    </v-btn>
  </v-app-bar>
</template>

<script>
  // Components
  // Utilities
  import { mapMutations, mapState } from 'vuex'

  const config = require('@/assets/config.json')
  export default {
    name: 'DashboardCoreAppBar',

    props: {
      value: {
        type: Boolean,
        default: false,
      },
    },

    data: function () {
      return ({
        notifications: [
          this.$i18n.t('zh'),
          this.$i18n.t('en'),
          this.$i18n.t('de'),
        ],
        profile: [
          { title: 'Log out' },
        ],
      })
    },
    computed: {
      ...mapState(['drawer']),
    },

    methods: {
      changeLang (i) {
        console.log(i)
        console.log(this.$i18n.locale)
        this.$i18n.locale = config.supportLang[i].lang
        localStorage.setItem('lang', this.$i18n.locale)
        this.$router.go(0)
      },
      ...mapMutations({
        setDrawer: 'SET_DRAWER',
      }),
    },
  }
</script>
